"use client";
import { useState } from "react";
import { Search, Edit, Trash2, Plus } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";

type Category = "information" | "locations" | "providers" | "types" | "categories";

type DataItem = {
  id: string;
  name: string;
  description?: string;
  status?: string;
  // Add other fields as needed
};

export default function PlanSettingsPage() {
  const [activeCategory, setActiveCategory] = useState<Category>("information");
  const [searchTerm, setSearchTerm] = useState("");
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [newItem, setNewItem] = useState({ name: "", description: "" });

  // Hardcoded data for each category
  const data: Record<Category, DataItem[]> = {
    information: [
      { id: "1", name: "Plan Overview", description: "General plan information" },
      { id: "2", name: "Budget Details", description: "Financial information" },
      { id: "3", name: "Timeline", description: "Project schedule" },
    ],
    locations: [
      { id: "1", name: "North Region", status: "Active" },
      { id: "2", name: "South Region", status: "Active" },
      { id: "3", name: "East Wing", status: "Inactive" },
    ],
    providers: [
      { id: "1", name: "HealthCare Inc", status: "Approved" },
      { id: "2", name: "MediSolutions", status: "Pending" },
      { id: "3", name: "Nursing Pros", status: "Approved" },
    ],
    types: [
      { id: "1", name: "Full-Time", description: "40 hours/week" },
      { id: "2", name: "Part-Time", description: "20 hours/week" },
      { id: "3", name: "Contract", description: "Project-based" },
    ],
    categories: [
      { id: "1", name: "Clinical", description: "Medical staff" },
      { id: "2", name: "Administrative", description: "Support staff" },
      { id: "3", name: "Technical", description: "IT staff" },
    ],
  };

  const filteredData = data[activeCategory].filter(item =>
    item.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (item.description && item.description.toLowerCase().includes(searchTerm.toLowerCase()))
  );

  const handleAddItem = () => {
    // In a real app, you would save to your backend here
    alert(`Adding new ${activeCategory}: ${newItem.name}`);
    setIsAddModalOpen(false);
    setNewItem({ name: "", description: "" });
  };

  const handleEdit = (id: string) => {
    // Edit functionality would go here
    alert(`Editing ${activeCategory} item with ID: ${id}`);
  };

  const handleDelete = (id: string) => {
    // Delete functionality would go here
    alert(`Deleting ${activeCategory} item with ID: ${id}`);
  };

  return (
    <div className="container mx-auto px-4 py-8 text-black">
      <h1 className="text-2xl font-bold mb-8">Plan Settings</h1>

      {/* Category Selector */}
      <div className="grid grid-cols-2 md:grid-cols-5 gap-2 mb-8">
        <Button
          variant={activeCategory === "information" ? "default" : "outline"}
          onClick={() => setActiveCategory("information")}
          className="py-6 flex flex-col items-center"
        >
          <span className="text-lg">Information</span>
        </Button>
        <Button
          variant={activeCategory === "locations" ? "default" : "outline"}
          onClick={() => setActiveCategory("locations")}
          className="py-6 flex flex-col items-center"
        >
          <span className="text-lg">Locations</span>
        </Button>
        <Button
          variant={activeCategory === "providers" ? "default" : "outline"}
          onClick={() => setActiveCategory("providers")}
          className="py-6 flex flex-col items-center"
        >
          <span className="text-lg">Providers</span>
        </Button>
        <Button
          variant={activeCategory === "types" ? "default" : "outline"}
          onClick={() => setActiveCategory("types")}
          className="py-6 flex flex-col items-center"
        >
          <span className="text-lg">Types</span>
        </Button>
        <Button
          variant={activeCategory === "categories" ? "default" : "outline"}
          onClick={() => setActiveCategory("categories")}
          className="py-6 flex flex-col items-center"
        >
          <span className="text-lg">Categories</span>
        </Button>
      </div>

      {/* Search and Add Button */}
      <div className="flex flex-col md:flex-row justify-between gap-4 mb-6">
        <div className="relative w-full md:w-64">
          <Search className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
          <Input
            placeholder={`Search ${activeCategory}...`}
            className="pl-10"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
        <Button onClick={() => setIsAddModalOpen(true)} className="gap-2">
          <Plus className="h-4 w-4" />
          Add {activeCategory.charAt(0).toUpperCase() + activeCategory.slice(1)}
        </Button>
      </div>

      {/* Data Table */}
      <div className="rounded-md border overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-100">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Name
                </th>
                {activeCategory === "information" || activeCategory === "types" || activeCategory === "categories" ? (
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Description
                  </th>
                ) : null}
                {activeCategory === "locations" || activeCategory === "providers" ? (
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                ) : null}
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredData.length > 0 ? (
                filteredData.map((item) => (
                  <tr key={item.id}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {item.name}
                    </td>
                    {activeCategory === "information" || activeCategory === "types" || activeCategory === "categories" ? (
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {item.description}
                      </td>
                    ) : null}
                    {activeCategory === "locations" || activeCategory === "providers" ? (
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        <span className={`px-2 py-1 rounded-full text-xs ${
                          item.status === "Active" || item.status === "Approved"
                            ? "bg-green-100 text-green-800"
                            : "bg-yellow-100 text-yellow-800"
                        }`}>
                          {item.status}
                        </span>
                      </td>
                    ) : null}
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      <div className="flex space-x-2">
                        <Button
                          variant="ghost"
                          size="icon"
                          onClick={() => handleEdit(item.id)}
                          className="text-blue-600 hover:text-blue-800"
                        >
                          <Edit className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="ghost"
                          size="icon"
                          onClick={() => handleDelete(item.id)}
                          className="text-red-600 hover:text-red-800"
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={4} className="px-6 py-4 text-center text-sm text-gray-500">
                    No {activeCategory} found matching your search.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Add Modal */}
      {isAddModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 w-full max-w-md">
            <h2 className="text-xl font-bold mb-4">
              Add New {activeCategory.charAt(0).toUpperCase() + activeCategory.slice(1)}
            </h2>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium mb-1">Name</label>
                <Input
                  value={newItem.name}
                  onChange={(e) => setNewItem({...newItem, name: e.target.value})}
                  placeholder={`Enter ${activeCategory} name`}
                />
              </div>
              {(activeCategory === "information" || activeCategory === "types" || activeCategory === "categories") && (
                <div>
                  <label className="block text-sm font-medium mb-1">Description</label>
                  <Input
                    value={newItem.description}
                    onChange={(e) => setNewItem({...newItem, description: e.target.value})}
                    placeholder="Enter description"
                  />
                </div>
              )}
              <div className="flex justify-end space-x-2 pt-4">
                <Button variant="outline" onClick={() => setIsAddModalOpen(false)}>
                  Cancel
                </Button>
                <Button onClick={handleAddItem}>
                  Add
                </Button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}